.datatable {
  height: 600px;
  padding: 20px;
  .data {
    @media (max-width: 992px) {
      // overflow-x: auto;
    }
  }

  .tableTitle {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 24px;
    color: gray;
    margin-bottom: 10px;

    .link {
      text-decoration: none;
      color: green;
      font-size: 18px;
      font-weight: 400;
      border: 1px solid green;
      padding: 5px;
      border-radius: 5px;
      cursor: pointer;
    }
  }
  .cellWithImg {
    display: flex;
    align-items: center;
    img {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      object-fit: cover;
      margin-right: 20px;
    }
  }
  .cellWithStatus {
    padding: 5px;
    border-radius: 5px;
    &.active {
      background-color: rgba(0, 128, 0, 0.5);
      color: green;
    }
    &.pending {
      background-color: rgba(255, 217, 0, 0.5);
      color: goldenrod;
    }
    &.passive {
      background-color: rgba(255, 0, 0, 0.5);
      color: crimson;
    }
  }
  .cellAction {
    display: flex;
    align-items: center;
    gap: 15px;
    .view {
      padding: 2px 5px;
      border-radius: 5px;
      color: darkblue;
      border: 1px dotted rgba(0, 0, 139, 0.596);
      cursor: pointer;
    }
    .delete {
      padding: 2px 5px;
      border-radius: 5px;
      color: crimson;
      border: 1px dotted rgba(220, 20, 60, 0.6);
      cursor: pointer;
    }
  }
}
